<script>
/*eslint-disable*/
import AppFooter from "../components/AppFooter";
import AppNav from "../components/AppNav";
import videoUrl from "../static/home.mp4"
export default {
  name: "Home",
  data() {
    return {
      navPosition:"relative",
      bgOpacity:0,
      videoUrl:videoUrl,
      isShowView:false,
      activeIndex:0
    }
  },
  components: {
    AppNav,
    AppFooter
  },
  created() {

  },
  methods: {

  },
  async mounted() {
    let timer = null
    let _this = this
    setTimeout(()=>{
      this.isShowView = true
    },500)
    window.addEventListener('scroll', function(e) {
      if(timer) {
        return
      }
      var targetRect = document.getElementById('page1').offsetTop
      console.log(targetRect,)

      timer = setTimeout(() => {
        if((parseInt(document.documentElement.scrollTop) -parseInt(targetRect)) / targetRect >0){
          _this.bgOpacity = (parseInt(document.documentElement.scrollTop) -parseInt(targetRect)) / targetRect
          _this.navPosition = "fixed"
          _this.activeIndex = 1
        }else{
          _this.navPosition = ""
          _this.activeIndex = 0
        }


        timer = null
      }, 10)
    })
  }
}
</script>

